* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #d1cece;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.big-img {
  margin-right: 30px;
}
.country-info_name {
  margin-left: 10px;
  font-size: 60px;
  margin-bottom: 0;
}
.country-info_item {
  font-size: 30px;
  margin: 10px;
}
span {
  font-weight: bold;
}
.country-list {
  list-style: none;
}
.country-list_item {
  display: flex;
  align-items: center;
}
.country-list_name{
  font-size: 20px;
  margin-left: 20px;
  font-weight: 600;
}
.mini-img {
  width: 50px; 
  height: 40px;
}
.title {
  font-size: medium;
}